<template>
    <div class="box">
        <!-- <Header  :nnt="false" :got="true" :ont="true" :gnt="false"> -->
        <Header :got="true" :mnt="true" :gnt="false"></Header>
        <div class="content">
            <ul>
                <li>
                    <van-cell-group>
                        <van-field v-model="address" class="but" placeholder="请填写你的名字" />
                    </van-cell-group>
                </li>
                <li>
                    <van-cell-group>
                        <router-link to="/searchadd">
                            <van-field v-model="address_detail" class="but" placeholder="小区/写字楼/学校等" />
                        </router-link>
                        
                    </van-cell-group>
                </li>
                <li>
                    <van-cell-group>
                        <van-field v-model="name" class="but" placeholder="请填写详细送餐地址" />
                    </van-cell-group>
                </li>
                <li>
                    <van-cell-group>
                        <van-field v-model="phone" class="but" placeholder="请填写能够联系到你的手机号" />
                    </van-cell-group>
                </li>
                <li>
                    <van-cell-group>
                        <van-field v-model="phone_bk" class="but" placeholder="备用联系电话（选填）" />
                    </van-cell-group>
                </li>
                <li><van-button type="primary" class="receiving" @click="add()">新增地址</van-button></li>
            </ul>
        </div>
    </div>
</template>

<script>
    import Header from '../header/Header'
    export default {
        data() {
            return {
                address: "",
                address_detail: "",
                geohash: "",
                name: "",
                phone: "",
                phone_bk: "",
                poi_type: 0,
                sex: 1,
                tag: "公司",
                tag_type: 4
            }
        },
        components: {
            Header
        },
        methods: {
            add(){
                this.http.post("http://elm.cangdu.org/v1/users/38070/addresses",{
                    address:this.address,
                    address_detail:this.address_detail,
                    geohash:this.geohash,
                    name:this.name,
                    phone:this.phone,
                    phone_bk:this.phone_bk,
                    poi_type:this.poi_type,
                    sex:this.sex,
                    tag:this.tag,
                    tag_type:this.tag_type
                }).then((res)=>{
                   this.$router.push("/anout/receiv")
                })
                
            }
        },
        mounted() {
            this.address_detail=this.$route.query.name.name
            this.geohash=this.$route.query.name.geohash
        }, 
    }
</script>

<style lang="scss" scoped>
    .box{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        font-size: 0.35rem;
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        .but{
            width: 94%;
            margin: 0.2rem 3%;
            background: rgb(243, 237, 237);
            border-radius: 0.1rem;
        }
    }
    .receiving{
        width: 94%;
        margin: 0.1rem 3%;
    }
</style>


